  .search-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
  }
  
  input[type='text'] {
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    margin-right: 10px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.5);
  }
  
  input[type='text']:focus {
    outline: none;
    box-shadow: 0 0 6px rgba(0, 0, 255, 0.5);
  }
  
  .invite-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: bold;
    padding: 10px 20px;
    cursor: pointer;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.5);
    transition: all 0.2s ease-in-out;
  }
  
  .invite-button:hover {
    background-color: #0056b3;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  
  .invite-button:focus {
    outline: none;
    box-shadow: 0 0 6px rgba(0, 0, 255, 0.5);
  }

  .circle {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: #007bff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.5);
    transition: all 0.2s ease-in-out;
    cursor: pointer;
  }
  
  .circle.open {
    width: 300px;
    height: 80px;
    border-radius: 5px;
  }
  
  .circle.open .icon {
    display: block;
    width: 50px;
    height: 50px;
    margin-right: 10px;
    font-size: 24px;
    transition: all 0.2s ease-in-out;
  }
  
  .circle.open .text {
    display: none;
  }
  
  .circle.open .search {
    display: block;
    width: 220px;
    height: 80px;
    margin-left: 10px;
    opacity: 0;
    transition: all 0.2s ease-in-out;
  }
  
  .circle.open .search input[type='text'] {
    width: 150px;
    height: 40px;
    margin-right: 10px;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.5);
    transition: all 0.2s ease-in-out;
  }
  
  .circle.open .search button {
    width: 60px;
    height: 40px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    background-color: #28a745;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.5);
    transition: all 0.2s ease-in-out;
  }
  
  .circle.open .search input[type='text']:focus,
  .circle.open .search button:focus {
    outline: none;
    box-shadow: 0 0 6px #28a745;
  }
  
  .circle.open .search input[type='text']:focus {
    width: 200px;
  }
  
  .circle.open .search button:hover {
    background-color: #218838;
  }
  
  .circle.open .search {
    opacity: 1;
  }
  
  .circle.hide {
    display: none;
  }

